import PropTypes from 'prop-types';
import { Panel, Div , Button , PanelHeader , View , ScreenSpinner , ModalPage , ModalRoot , ModalPageHeader , HeaderButton
	, List , Cell , InfoRow , Progress , Gallery , Counter , Placeholder , Snackbar , Avatar , Group , Separator , Alert
	, Tooltip , Tabs , TabsItem, platform, IOS, ANDROID  } from '@vkontakte/vkui';

