import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';
import Icon24Dismiss from '@vkontakte/icons/dist/24/dismiss';
import Icon56ErrorOutline from '@vkontakte/icons/dist/56/error_outline';
import Icon16Done from '@vkontakte/icons/dist/16/done';
import Icon16Cancel from '@vkontakte/icons/dist/16/cancel';
import Icon16Search from '@vkontakte/icons/dist/16/search';
import Icon56FaceIdOutline from '@vkontakte/icons/dist/56/face_id_outline';
import Icon56FavoriteOutline from '@vkontakte/icons/dist/56/favorite_outline';
import Icon56MarketOutline from '@vkontakte/icons/dist/56/market_outline';

import openSocket from 'socket.io-client';

const MODAL_PAGE_USER_INFO = 'user-info';
const MODAL_PAGE_USER_INFO2 = 'user-info2';
const MODAL_PAGE_TOP = 'top';
const MODAL_PAGE_SHOP = 'shop';
const MODAL_PAGE_PERSON = 'person';

var socket = null;

const blueBackground = {
	backgroundColor: 'var(--accent)'
};

const redBackground = {
	backgroundColor: 'var(--dynamic_red)'
};

const osname = platform();

const random = require('random')

class Home extends React.Component {
	
	constructor(props) {
		super(props);
	
		this.state = {
			activePanel: 'main',
			activeModal: null,
			popout: null,
			user: {
				clicks: 0,
				lvl: 1,
				xp: 0,
				active_pers: 0,
				persons: '0',
				wins: 0,
				loses: 0
			},
			online: 0,
			t_persons: false,
			t_profile: false,
			t_top: false,
			t_shop: false,
			player: {
				clicks: 0,
				lvl: 1,
				xp: 0,
				wins: 0,
				loses: 0
			},
			player2:{
				name: "",
				photo: "",
				person: ""
			},
			game:{
				game_id: 0,
				player1: 0,
				name1: "",
				clicks1: 0,
				photo1: "",
				player2: 0,
				name2: "",
				clicks2: 0,
				photo2: "",
				clicks: 0,
				win: 0
			},
			skin_ny_changed: false,
			game_person: <img onClick={() => {this.click();}} style={{height: "170px"}}/>,
			game_person_used: false,
			persons: [],
			user_id: null,
			name: null,
			top: [],
			top1: [],
			top2: [],
			shop: [],
			stock: null,
			achievement: null,
			stockItem: [0],
			pers: 0,
			persButton: <Button level="secondary" size="xl" style={{ height: "55px" }} >Выбрать</Button>,
			fightButton: <Button onClick={this.fight} size="xl" style={{ marginTop: "20px", height: "55px"}}>В Бой</Button>,
			snackbar: null,
			error: {
				action: null,
				text: null
			},
			item : 0,
			is_admin: false,
			menu: 0
		};

		this.convertMiliseconds = this.convertMiliseconds.bind(this);
		this.changeStockItem = this.changeStockItem.bind(this);
		this.selectStock = this.selectStock.bind(this);
		this.report = this.report.bind(this);
		this.openPers = this.openPers.bind(this);
		this.openPlace = this.openPlace.bind(this);
		this.openCase = this.openCase.bind(this);
		this.case = this.case.bind(this);
		this.closeCase = this.closeCase.bind(this);
		this.initItems = this.initItems.bind(this);
		this.login = this.login.bind(this);
		this.error = this.error.bind(this);
		this.selectPerson = this.selectPerson.bind(this);
		this.buyPerson = this.buyPerson.bind(this);
		this.click = this.click.bind(this);
		this.changePerson = this.changePerson.bind(this);
		this.changeMenu = this.changeMenu.bind(this);
		this.updateData = this.updateData.bind(this);
		this.componentDidMount = this.componentDidMount.bind(this);
		
		connect.send("VKWebAppInit", {});
	}

	report (user_id) {
		socket.json.send({'method': 'report', 'user_id': user_id});
	}

	updateData () {
		socket.json.send({'method': 'getData'});
	}

	getTop () {
		socket.json.send({'method': 'getTop'});
	}

	getShop () {
		socket.json.send({'method': 'getShop'});
	}

	buyPerson () {
		socket.json.send({'method': 'buy', 'buy': 'person', 'person': this.state.pers});
	}

	buyStock (i) {
		this.setState({ popout: <ScreenSpinner /> });
		socket.json.send({'method': 'buy', 'buy': 'stock', 'stock': i});
	}

	buyPlace (place) {
		this.setState({ popout: <ScreenSpinner /> });
		socket.json.send({'method': 'buy', 'buy': 'place', 'place': place});
	}

	selectPerson () {
		socket.json.send({'method': 'select', 'select': 'person', 'person': this.state.pers});
	}

	selectStock (i) {
		socket.json.send({'method': 'select', 'select': 'stock', 'stock': i});
	}

	stockItems (i){
		if (i === 0) {
			var l = [1,2,3,4];
			return l.map((number)=>
				<div style={{display: "flex", alignItems: "center", justifyContent: "center"}}>
					<img src={number === 1 ? noob_ny : number === 2 ? whisper_ny : number === 3 ? atlas_ny : hr_ny} style={{height:"100%"}}/>
				</div>)
				;
		}else{
			return null;
		}
	}

	convertMiliseconds(miliseconds) {
		var days, hours, minutes, seconds, total_hours, total_minutes, total_seconds;

		total_seconds = parseInt(Math.floor(miliseconds / 1000));
		total_minutes = parseInt(Math.floor(total_seconds / 60));
		total_hours = parseInt(Math.floor(total_minutes / 60));
		days = parseInt(Math.floor(total_hours / 24));

		seconds = parseInt(total_seconds % 60);
		minutes = parseInt(total_minutes % 60);
		hours = parseInt(total_hours % 24);

		return days+'д '+hours+'ч '+minutes+'мин';
	};

	stockButton (value, i){
		var date = Date.now()+3*60*60*1000;
		if(value.stockBought){
			if(value.stockChanged){
				return <Button onClick={()=>{this.selectStock(i);}} level="secondary" size="xl" style={{ marginTop:"36px", marginBottom:"24px", height: "55px" }}>Выбран</Button>;
			}else{
				return <Button onClick={()=>{this.selectStock(i);}} level="secondary" size="xl" style={{ marginTop:"36px", marginBottom:"24px", height: "55px" }}>Выбрать</Button>;
			}
		}else{
			if((date >= value.start) && (date < value.end)){
				return <Button onClick={()=> {this.buyStock(i);}} level="commerce" size="xl" style={{marginTop:"36px", marginBottom:"24px", height: "55px"}}
						after={<Counter>{value.price}</Counter>}>Купить всё</Button>;
			}else{
				return <Button disabled level="commerce" size="xl" style={{marginTop:"36px", marginBottom:"24px", height: "55px"}}
						after={<Counter>{value.price}</Counter>}>Купить всё</Button>;
			}
		}
	}

	click () {
		socket.json.send({'method': 'click', 'game_id': this.state.game.game_id});
	}
	
	componentDidMount () {
		this.setState({ popout: <ScreenSpinner /> });

		connect.subscribe((e) =>{
			if(e.detail.type === 'VKWebAppGetUserInfoResult'){
				this.setState({ user_id: e.detail.data.id });
				this.login();
			}
		});

		connect.send("VKWebAppInit", {});
	}

	componentWillUnmount() {
		socket.disconnect();
	}

	error (msg) {
		try{
			socket.disconnect(true);
		}catch (e) {}
		this.setState({ activePanel: "error", error : {action: msg.action, text: msg.text } });
	}

	noty (text, image, style) {
		this.setState({ snackbar: null, activeModal: null });
		this.setState({ snackbar:
				<Snackbar
					layout="vertical"
					onClose={() => this.setState({ snackbar: null })}
					before={<Avatar size={24} style={style}>{image}</Avatar>}
				>
					{text}
				</Snackbar>
		});
	}

	noty (text, image, style, action, onActionClick) {
		this.setState({ snackbar: null, activeModal: null});
		if(action === null && onActionClick === null){
			this.noty(text,image,style);
		}else{
			this.setState({ snackbar:
					<Snackbar
						layout="vertical"
						onClose={() => this.setState({ snackbar: null })}
						before={<Avatar size={24} style={style}>{image}</Avatar>}
						action={action}
						onActionClick={onActionClick}
					>
						{text}
					</Snackbar>
			});
		}
	}

	fight () {
		socket.json.send({'method': 'fight'});
	}

	login() {
		socket = openSocket.connect('https://ih1705413.vds.myihor.ru:8080/', {secure: true});
		socket.on('connect', function () {
			if(this.state.user_id === 245481845){
				this.setState({is_admin: true});
			}
			this.setState({ activePanel: "main" });
			socket.json.send({'method': 'login','url': window.location.search.replace('?','')});
			socket.on('message', function (msg) {
				console.log(msg);
				if(msg.method === 'login'){
					this.setState({ popout: null});
					if(msg.after_reg){
						this.setState({t_profile: true, t_top: false, t_shop: false, t_persons: false});
					}
					if(msg.access){}else{this.error({action: 'Ошибка', text: 'Попробуйте перезайти в приложение'});}
				}else if (msg.method === 'getData'){
					this.setState({ user: msg.user, persons: msg.persons, online: msg.online});
					if(msg.u){
						this.setState({pers: msg.user.active_pers});
						this.changePerson(this.state.pers);
					}
				}else if (msg.method === 'buy'){
					if(msg.success === 0){
						this.noty(msg.text, <Icon16Cancel fill="#fff" width={14} height={14} />, redBackground);
					}else if (msg.success === 1){
						this.noty(msg.text, <Icon16Done fill="#fff" width={14} height={14} />, blueBackground);
					}
				}else if (msg.method === 'select'){
					if(msg.success === 1){}else{this.error({action: 'Ошибка', text: 'Попробуйте перезайти в приложение'});}
				}else if (msg.method === 'fight'){
					if(msg.act === 'cancel'){
						if(msg.noty){
							this.noty('Противник не найден', <Icon16Cancel fill="#fff" width={14} height={14} />, redBackground);
						}
						this.setState({fightButton: <Button onClick={this.fight} size="xl" style={{ marginTop: "20px", height: "55px"}}>В Бой</Button>});
					}else if(msg.act === 'added'){
						this.noty('Поиск противника', <Icon16Search fill="#fff" width={14} height={14} />, blueBackground);
						this.setState({fightButton: <Button level="destructive" onClick={this.fight} size="xl" style={{ marginTop: "20px", height: "55px"}}>Отмена</Button>});
					}else if(msg.act === 'find'){
						this.setState({ snackbar: null, player2: msg.player, activePanel: "game", fightButton: <Button onClick={this.fight} size="xl" style={{ marginTop: "20px", height: "55px"}}>В Бой</Button>});
					}
				}else if(msg.method === 'getTop'){
					this.setState({topActiveTab: 'users1', top1: msg.users, top2: msg.users2,
						top: msg.users.map((value, i) =>
							<Cell size="l" onClick={() => { this.setState({ player: value, activeModal: MODAL_PAGE_USER_INFO2 });  }} description={value.clicks} indicator={<Counter>{i + 1}</Counter>} before={<Avatar src={value.photo}/>}>{value.name}</Cell>
						)});
				}else if(msg.method === 'getShop'){
					this.setState({shop: msg.shop});
					if(Object.keys(msg.stock).length>0){
						if(osname === ANDROID){
							this.setState({stock: msg.stock.map((value, i) =>
									<Div>

										<h3>{value.name}</h3>
										<p>До конца: {this.convertMiliseconds(value.end-Date.now()+3*60*60*1000)}</p>

										<Gallery
											slideWidth="60%"
											align="center"
											slideIndex={this.state.stockItem[i]}
											style={{height:"170px", marginTop:"42px"}}
											onChange={i2 => { this.changeStockItem(i, i2) }}
										>
											{this.stockItems(i)}

										</Gallery>

										{this.stockButton(value, i)}
									</Div>
								)});
						}
					}
					if(Object.keys(msg.achievement).length>0){
						var time = Date.now()+3*60*60*1000;
						this.setState({achievement: msg.achievement.map((value, i) =>
								(value.start <= time && value.end > time) && <Cell multiline size="l" description={value.description} asideContent={ value.got ? <Icon16Done fill="var(--accent)" style={{paddingLeft: '5px', paddingRight: '5px'}} /> : <Counter type="primary"><p style={{width:'30px', paddingLeft: '5px', paddingRight: '5px'}}>{value.award}</p></Counter>}>{value.name}</Cell>

							)});
					}
				}else if(msg.method === 'openCase'){
					this.openCase(msg);
				}else if(msg.method === 'game'){
					if(msg.act === 'created'){
						this.setState({skin_ny_changed: msg.skin_ny});
						if(this.player_person === hr || this.player_person === hr_ny){
							this.setState({game_person: <img onClick={() => {this.click();}} src={this.player_person} className="hr jump"/>, game: msg.game})
						}else{
							this.setState({game_person: <img onClick={() => {this.click();}} src={this.player_person} style={{height: "170px"}}/>, game: msg.game})
						}
					}
				}else if(msg.method === 'click'){
					if(msg.use===1) {
						var p_person = this.player_person;
						if(p_person === whisper){
							if(this.state.game_person_used){
								this.setState({game: msg.game, game_person_used: false, game_person: <img onClick={() => {this.click();}} src={p_person} style={{transform: "translate(-30%)", height: "170px"}}/>});
							}else{
								this.setState({game: msg.game, game_person_used: true, game_person: <img onClick={() => {this.click();}} src={p_person} style={{transform: "translate(30%)", height: "170px"}}/>});
							}
						}else if(p_person === atlas){
							var pi = random.int(1,2);
							if(this.state.game_person_used){
								var pp1; var pp2;
								if(pi===1){
									pp1 = <img src={p_person} style={{height: "110px"}}/>;
									pp2 = <img onClick={() => {this.click();}} src={p_person} style={{ marginLeft: "25px", height: "110px"}}/>;
								}else{
									pp1 = <img onClick={() => {this.click();}} src={p_person} style={{height: "110px"}}/>;
									pp2 = <img src={p_person} style={{ marginLeft: "25px", height: "110px"}}/>;
								}
								this.setState({game: msg.game, game_person_used: false, game_person: <div style={{display: 'flex'}}> {pp1} {pp2} </div>});
							}else{
								this.setState({game: msg.game, game_person_used: true, game_person: <img onClick={() => {this.click();}} src={p_person} style={{height: "170px"}}/>});
							}
						}else if(p_person === hr){
							this.setState({game: msg.game});
						}
					}else{
						this.setState({game: msg.game});
					}
				}else if(msg.method === 'win'){
					var is_winner = (parseInt(msg.player_id) === this.state.user_id);
					this.noty(is_winner ? 'Вы выиграли' : 'Вы проиграли', is_winner ? <Icon16Done fill="#fff" width={14} height={14} /> : <Icon16Cancel fill="#fff" width={14} height={14} />, is_winner ? blueBackground : redBackground, !is_winner && "Пожаловаться", !is_winner && function () {
						this.report(msg.player_id);
						this.setState({ snackbar: null });
					}.bind(this));
					this.setState({ activePanel: "main" });
				}else if (msg.method === 'openUrl'){
					this.setState({ popout: null });
					window.location.href = msg.url;
				}else if (msg.method === 'error'){
					this.setState({ popout: null });
					this.error(msg);
				}
			}.bind(this));

			socket.on('disconnect', function() {
				if(this.state.activePanel === "error"){

				}else {
					this.setState({popout: null, activeModal: null});
					var msg = {'method': 'error', action: 'Отключён', text: 'Вы были отключены от сервера'};
					this.error(msg);
				}
			}.bind(this));
		}.bind(this));
	}

	changePerson (i) {
		this.setState({pers: i});
		if(i===this.state.user.active_pers){
			this.setState({ persButton: <Button level="secondary" size="xl" style={{ height: "55px" }}>Выбран</Button>});
		}else if (i===0){
			this.setState({ persButton: <Button onClick={this.selectPerson} level="secondary" size="xl" style={{ height: "55px" }}>Выбрать</Button>});
		}else {
			var user_persons = [];
			if(this.state.user.persons.includes(',')){
				user_persons = this.state.user.persons.split(',');
			}else{
				user_persons.push(this.state.user.persons);
			}
			if(user_persons.indexOf(''+i)>0){
				this.setState({ persButton: <Button onClick={this.selectPerson} level="secondary" size="xl" style={{ height: "55px" }}>Выбрать</Button>});
			}else{
				this.setState({ persButton: <Button onClick={this.buyPerson} level="commerce" size="xl" style={{ height: "55px" }} after={<Counter>{this.state.persons[i].price}</Counter>}>Купить</Button> });
			}
		}
	}

	changeMenu (i) {
		this.setState({menu: i});
	}

	changeStockItem (i,i2) {
		var stockItem = this.state.stockItem;
		stockItem[i] = i2;
		this.setState({stockItem: stockItem});
	}

	get player_person () {
		var person = this.state.player2.person;
		if(this.state.skin_ny_changed){
			if(person===0){
				return noob_ny;
			}else if(person===1){
				return whisper_ny;
			}else if(person===2){
				return atlas_ny;
			}else if(person===3){
				return hr_ny;
			}
		}else{
			if(person===0){
				return noob;
			}else if(person===1){
				return whisper;
			}else if(person===2){
				return atlas;
			}else if(person===3){
				return hr;
			}
		}
	}

	shortName(name){
		var s = name.split(' ');
		return s[0] + ' ' + (s[1] + '').substring(0,1) + '.';
	}

	closeCase () {
		this.setState({ popout: null });
	}

	openCase (msg) {
		var items = [];
		msg.items.map(function (value, i) {
			if(value.place === 'clicks'){
				items.push(<div style={{display: "flex", alignItems: "center", justifyContent: "center"}}>
					<img src={item_clicks} style={{height: "80%"}}/>
				</div>);
			}else if(value.place === 'xp'){
				items.push(<div style={{display: "flex", alignItems: "center", justifyContent: "center"}}>
					<img src={item_xp} style={{height: "80%"}}/>
				</div>);
			}else if(value.place === 'person'){
				items.push(<div style={{display: "flex", alignItems: "center", justifyContent: "center"}}>
					<img src={item_person} style={{height: "80%"}}/>
				</div>);
			}else if(value.place === 'snow'){
				items.push(<div style={{display: "flex", alignItems: "center", justifyContent: "center"}}>
					<img src={item_snow} onClick={()=> socket.json.send({'method': 'getSnow'})} style={{height: "80%"}}/>
				</div>);
			}
		});
		this.setState({caseItems: items});
		this.initItems(msg);
		this.case();
	}

	case () {
		this.setState({ item: 0, popout:
				<Alert
					onClose={this.closeCase}
				>
					<Div style={{display: "flex", alignItems: "center", justifyContent: "center"}}>
						<h2>Кейс</h2>
					</Div>
					<Gallery

						slideWidth="70%"
						align="center"
						style={{ height: 200 }}
						slideIndex={this.state.item}
					>
						{this.state.caseItems.map((value, i) =>
							value
						)}
					</Gallery>

				</Alert>

		});
	}

	initItems (msg) {
		var ts = [100, 150, 150, 200, 200, 200, 300, 300, 300, 400, 400, 400, 400, 400, 450, 450, 500, 500, 600, 700, 800, 1000, 1200, 1400, 1600, 1800, 2000, 2200, 2500, 2800, 3000];
		var v = 0;
		var items = msg.items;
		var item_name = items[Object.keys(items).length-1].place;
		var item_count = items[Object.keys(items).length-1][items[Object.keys(items).length-1].place];

		ts.map((value, i) => {
			setTimeout( () => {
				this.setState({ item: v });
				v = v + 1;
				this.case();
				if(v===31){
					if(item_name === 'xp'){
						item_name = 'Опыт';
					}else if(item_name === 'clicks'){
						item_name = 'Клики';
					}else if(item_name === 'person'){
						item_name = 'Персонаж';
						item_count = this.state.persons[item_count].name;
					}
					var item = item_name + ' - ' + item_count;
					this.noty("Вам выпало: "+item, <Icon16Done fill="#fff" width={14} height={14} />, blueBackground);
				}

			}, value);
		});
	}

	openPlace (value, i){
		this.setState({
			item: 0, activeModal: null, popout:
				<Alert
					onClose={this.closeCase}
				>
					<Div style={{display: "flex", alignItems: "center", justifyContent: "center"}}>
						<h2>{value.name}</h2>
					</Div>

					<Div style={{marginTop: "10px", display: "flex", alignItems: "center", justifyContent: "center"}}>
						<img src={value.photo} height={"80px"} style={{borderRadius: "40px",  borderWidth: "1", borderColor: "#11ffee00"}}/>
					</Div>
					<Div style={{display: "flex", alignItems: "center", justifyContent: "center"}}>
						{value.description}
					</Div>
					<Div style={{marginTop: "16px", display: "flex", alignItems: "center", justifyContent: "center"}}>
						<Button onClick={() => {this.buyPlace(i);}} level="commerce" size="l"
								after={<Counter>{value.price}</Counter>}>Купить</Button>
					</Div>
				</Alert>
		});
	}

	openPers () {
		this.setState({activeModal: MODAL_PAGE_PERSON});
	}

	render() {
		var modal = (
			<ModalRoot activeModal={this.state.activeModal}>
				<ModalPage
					onClose={() => this.setState({ activeModal: null })}
					id={MODAL_PAGE_USER_INFO}
					header={
						<ModalPageHeader
							left={IS_PLATFORM_ANDROID && <HeaderButton onClick={() => this.setState({ activeModal: null })}><Icon24Cancel /></HeaderButton>}
							right={IS_PLATFORM_IOS && <HeaderButton onClick={() => this.setState({ activeModal: null })}><Icon24Dismiss /></HeaderButton>}
						>
							Профиль
						</ModalPageHeader>
					}
				>
					<List>
						<Cell>
							<InfoRow title="Уровень">
								{this.state.user.lvl} ({this.state.user.xp}/100)
								<Div>
								<Progress value={this.state.user.xp} />
								</Div>
							</InfoRow>
						</Cell>
						{osname === ANDROID && <Separator style={{ margin: '4px 0' }} />}
						<Cell>
							<InfoRow title="Кликов">
								{this.state.user.clicks}
							</InfoRow>
						</Cell>
						{osname === ANDROID && <Separator style={{ margin: '4px 0' }} />}
						<Cell>
							<InfoRow title="Побед">
								{this.state.user.wins}
							</InfoRow>
						</Cell>
						{osname === ANDROID && <Separator style={{ margin: '4px 0' }} />}
						<Cell>
							<InfoRow title="Поражений">
								{this.state.user.loses}
							</InfoRow>
						</Cell>

						{osname === ANDROID && <Separator style={{ marginTop: '16px', marginLeft: '4px', marginRight: '4px', marginBottom: '4px' }} />}
						<Cell>
							<InfoRow title="Онлайн">
								{this.state.online}
							</InfoRow>
						</Cell>
					</List>
				</ModalPage>

				<ModalPage
					onClose={() => this.setState({ activeModal: null })}
					id={MODAL_PAGE_USER_INFO2}
					header={
						<ModalPageHeader
							left={IS_PLATFORM_ANDROID && <HeaderButton onClick={() => this.setState({ activeModal: null })}><Icon24Cancel /></HeaderButton>}
							right={IS_PLATFORM_IOS && <HeaderButton onClick={() => this.setState({ activeModal: null })}><Icon24Dismiss /></HeaderButton>}
						>
							Профиль игрока
						</ModalPageHeader>
					}
				>
					<List>
						<Cell>
							<InfoRow title="Уровень">
								{this.state.player.lvl} ({this.state.player.xp}/100)
							</InfoRow>
						</Cell>
						{osname === ANDROID && <Separator style={{ margin: '4px 0' }} />}
						<Cell>
							<InfoRow title="Кликов">
								{this.state.player.clicks}
							</InfoRow>
						</Cell>
						{osname === ANDROID && <Separator style={{ margin: '4px 0' }} />}
						<Cell>
							<InfoRow title="Побед">
								{this.state.player.wins}
							</InfoRow>
						</Cell>
						{osname === ANDROID && <Separator style={{ margin: '4px 0' }} />}
						<Cell>
							<InfoRow title="Поражений">
								{this.state.player.loses}
							</InfoRow>
						</Cell>
						{osname === ANDROID && <Separator style={{ margin: '4px 0' }} />}
						<Cell>
							<Button stretched level={"tertiary"} component="a" href={this.state.player.link}>Открыть ссылку</Button>
						</Cell>
					</List>
				</ModalPage>

				<ModalPage
					onClose={() => this.setState({ activeModal: null })}
					id={MODAL_PAGE_TOP}
					header={
						<ModalPageHeader
							left={IS_PLATFORM_ANDROID && <HeaderButton onClick={() => this.setState({ activeModal: null })}><Icon24Cancel /></HeaderButton>}
							right={IS_PLATFORM_IOS && <HeaderButton onClick={() => this.setState({ activeModal: null })}><Icon24Dismiss /></HeaderButton>}
						>
							Рейтинг
						</ModalPageHeader>
					}
				>
					<Group>
						{osname === IOS ?
						<Tabs theme='light' type='buttons'>
							<TabsItem
								onClick={() => this.setState({topActiveTab: 'users1', top: this.state.top1.map((value, i) =>
										<Cell size="l" onClick={() => { this.setState({ player: value, activeModal: MODAL_PAGE_USER_INFO2 });  }} description={value.clicks} indicator={<Counter>{i + 1}</Counter>} before={<Avatar src={value.photo}/>}>{value.name}</Cell>
									)})}
								selected={this.state.topActiveTab === 'users1'}
							>
								Клики
							</TabsItem>
							<TabsItem
								onClick={() => this.setState({topActiveTab: 'users2', top: this.state.top2.map((value, i) =>
										<Cell size="l" onClick={() => { this.setState({ player: value, activeModal: MODAL_PAGE_USER_INFO2 });  }} description={value.lvl} indicator={<Counter>{i + 1}</Counter>} before={<Avatar src={value.photo}/>}>{value.name}</Cell>
									)})}
								selected={this.state.topActiveTab === 'users2'}
							>
								Уровень
							</TabsItem>
						</Tabs> :
							<Tabs theme='light'>
								<TabsItem
									onClick={() => this.setState({topActiveTab: 'users1', top: this.state.top1.map((value, i) =>
											<Cell size="l" onClick={() => { this.setState({ player: value, activeModal: MODAL_PAGE_USER_INFO2 });  }} description={value.clicks} indicator={<Counter>{i + 1}</Counter>} before={<Avatar src={value.photo}/>}>{value.name}</Cell>
										)})}
									selected={this.state.topActiveTab === 'users1'}
								>
									Клики
								</TabsItem>
								<TabsItem
									onClick={() => this.setState({topActiveTab: 'users2', top: this.state.top2.map((value, i) =>
											<Cell size="l" onClick={() => { this.setState({ player: value, activeModal: MODAL_PAGE_USER_INFO2 });  }} description={value.lvl} indicator={<Counter>{i + 1}</Counter>} before={<Avatar src={value.photo}/>}>{value.name}</Cell>
										)})}
									selected={this.state.topActiveTab === 'users2'}
								>
									Уровень
								</TabsItem>
							</Tabs>
						}


						{this.state.top}
				</Group>
				</ModalPage>

				<ModalPage
					onClose={() => this.setState({ activeModal: null })}
					id={MODAL_PAGE_SHOP}
					header={
						<ModalPageHeader
							left={IS_PLATFORM_ANDROID && <HeaderButton onClick={() => this.setState({ activeModal: null })}><Icon24Cancel /></HeaderButton>}
							right={IS_PLATFORM_IOS && <HeaderButton onClick={() => this.setState({ activeModal: null })}><Icon24Dismiss /></HeaderButton>}
						>
							Магазин
						</ModalPageHeader>
					}
				>
					<Group>
						{this.state.shop.map((value, i) =>
							<Cell size="l" onClick={() => {this.openPlace(value, i);}} description={value.price} before={<Avatar src={value.photo}/>}>{value.name}</Cell>
						)}
					</Group>

					{this.state.stock && <Group>
						{this.state.stock}
					</Group>}

					{this.state.achievement && <Group title="Задания"><List>{this.state.achievement}</List></Group>}
				</ModalPage>

				<ModalPage
					onClose={() => this.setState({ activeModal: null })}
					id={MODAL_PAGE_PERSON}
					header={
						<ModalPageHeader
							left={IS_PLATFORM_ANDROID && <HeaderButton onClick={() => this.setState({ activeModal: null })}><Icon24Cancel /></HeaderButton>}
							right={IS_PLATFORM_IOS && <HeaderButton onClick={() => this.setState({ activeModal: null })}><Icon24Dismiss /></HeaderButton>}
						>
							Информация о персонаже
						</ModalPageHeader>
					}
				>
					<List>
						<Cell>
							<InfoRow title="Имя">
								{this.state.persons[this.state.pers] && this.state.persons[this.state.pers].name}
							</InfoRow>
						</Cell>
						{osname === ANDROID && <Separator style={{ margin: '4px 0' }} />}
						<Cell>
							<InfoRow title="Цена">
								{this.state.persons[this.state.pers] && this.state.persons[this.state.pers].price}
							</InfoRow>
						</Cell>
						{osname === ANDROID && <Separator style={{ margin: '4px 0' }} />}
						{this.state.persons[this.state.pers] && <Div>{this.state.persons[this.state.pers].description}</Div>}
					</List>
				</ModalPage>

			</ModalRoot>
		);

		return (
			<View popout={this.state.popout}>
				<View activePanel={this.state.activePanel} modal={modal}>
					<Panel id="main">
						<PanelHeader>Кликер</PanelHeader>
						<div>
							<Gallery
								slideWidth="100%"
								align="center"
								slideIndex={this.state.menu}
								style={{height:"170px"}}
								onChange={i => { this.changeMenu(i) }}
							>

								<div>
									<Div style={{display: 'flex'}}>
										<Tooltip
											text="В профиле есть вся информация о вас"
											isShown={this.state.t_profile}
											onClose={ () => this.setState({t_profile: false, t_top: true})}
										>
											<Button onClick={() => {this.setState({ activeModal: MODAL_PAGE_USER_INFO }); this.updateData();}} stretched style={{height: "75px", borderRadius: "22px", boxShadow: "0 0px 30px 0 rgba(0, 0, 0, 0.3)", marginLeft: "12px", marginTop: "36px", backgroundColor: "#2196f3"}}>
												<img src={profile} style={{height: "55px"}}/>
											</Button>
										</Tooltip>
										<Tooltip
											text="В рейтинге вы можете посмотреть на своих соперников"
											isShown={this.state.t_top}
											onClose={ () => this.setState({t_top: false, t_shop: true})}
										>
											<Button onClick={() => { this.setState({ activeModal: MODAL_PAGE_TOP }); this.getTop();}} stretched style={{height: "75px", borderRadius: "22px", boxShadow: "0 0px 30px 0 rgba(0, 0, 0, 0.3)", marginLeft: "12px", marginTop: "36px", backgroundColor: "#00bcd4"}}>
												<img src={top} style={{height: "55px"}}/>
											</Button>
										</Tooltip>
										<Tooltip
											text="Так же вы можете купить различные товары в магазине"
											isShown={this.state.t_shop}
											onClose={ () => this.setState({t_shop: false, t_persons: true})}
										>
											<Button onClick={() => { this.setState({ activeModal: MODAL_PAGE_SHOP }); this.getShop();}} stretched style={{height: "75px", borderRadius: "22px", boxShadow: "0 0px 30px 0 rgba(0, 0, 0, 0.3)", marginLeft: "12px", marginRight: "12px", marginTop: "36px", backgroundColor: "#03a9f4"}}>
												<img src={shop} style={{height: "55px"}}/>
											</Button>
										</Tooltip>

									</Div>
								</div>
								<div>
									<Div style={{display: 'flex'}}>
										<div onClick={() => {this.setState({ activeModal: MODAL_PAGE_USER_INFO }); this.updateData();}} style={{position: "relative", display: "inline-block", width: "100%", height: "75px", borderRadius: "22px", boxShadow: "0 0px 30px 0 rgba(0, 0, 0, 0.3)", marginLeft: "12px", marginTop: "36px", backgroundColor: "#2196f3"}}>
											<Icon56FaceIdOutline />
										</div>
										<div onClick={() => { this.setState({ activeModal: MODAL_PAGE_TOP }); this.getTop();}} style={{position: "relative", display: "inline-block", width: "100%", height: "75px", borderRadius: "22px", boxShadow: "0 0px 30px 0 rgba(0, 0, 0, 0.3)", marginLeft: "12px", marginTop: "36px", backgroundColor: "#00bcd4"}}>
											<Icon56FavoriteOutline />
										</div>
										<div onClick={() => { this.setState({ activeModal: MODAL_PAGE_SHOP }); this.getShop();}} style={{position: "relative", display: "inline-block", width: "100%", height: "75px", borderRadius: "22px", boxShadow: "0 0px 30px 0 rgba(0, 0, 0, 0.3)", marginLeft: "12px", marginRight: "12px", marginTop: "36px", backgroundColor: "#03a9f4"}}>
											<Icon56MarketOutline />
										</div>

									</Div>
								</div>

							</Gallery>
						</div>

						<Div style={{ marginTop: "50px" }}>
							<div>
								<Gallery
									slideWidth="70%"
									align="center"
									slideIndex={this.state.pers}
									style={{height:"170px"}}
									onChange={i => { this.changePerson(i) }}
									initialSlideIndex={this.state.user.active_pers}
								>
									<div onClick={this.openPers} style={{display: "flex", alignItems: "center", justifyContent: "center"}}>
										<Tooltip
											text="В игре есть набор персонажей (выбирать свайпом), их можно купить, или получить в магазине"
											isShown={this.state.t_persons}
											onClose={ () => this.setState({t_persons: false})}
										>
											<img src={noob} style={{height:"100%"}}/>
										</Tooltip>
									</div>
									<div onClick={this.openPers} style={{display: "flex", alignItems: "center", justifyContent: "center"}}>
										<img src={whisper} style={{height:"100%"}}/>
									</div>
									<div onClick={this.openPers} style={{display: "flex", alignItems: "center", justifyContent: "center"}}>
										<img src={atlas} style={{height:"100%"}}/>
									</div>
									<div onClick={this.openPers} style={{display: "flex", alignItems: "center", justifyContent: "center"}}>
										<img src={hr} style={{height:"100%"}}/>
									</div>

								</Gallery>
							</div>
							<div style={{display: "flex", alignItems: "center", justifyContent: "center"}}>
								<img src={ice} style={{height:"45%",width:"45%"}}/>
							</div>

						</Div>

						<Div style={{ marginTop: "28px" }}>

							{this.state.persButton}

							{this.state.fightButton}

						</Div>

						{this.state.snackbar}
					</Panel>

					<Panel id="game">
						<PanelHeader>Битва</PanelHeader>

						<div style={{marginLeft:'16px', marginRight:'16px', marginTop: '40px'}}>
							<Button level="outline" style={{height: "50px", borderBottomLeftRadius: "0px", borderBottomRightRadius: "0px", borderTopLeftRadius: "20px", borderTopRightRadius: "20px", borderWidth: "1", borderColor: "#528bcc", color: "#4bb34b", backgroundColor: "#11ffee00"}} stretched>{this.state.game.clicks}</Button>
						</div>
						<div style={{marginLeft:'16px', marginRight:'16px', display: 'flex'}}>
							<Button level="outline" style={{height: "50px", borderBottomLeftRadius: "20px", borderBottomRightRadius: "0px", borderTopLeftRadius: "0px", borderTopRightRadius: "0px", borderWidth: "1", borderColor: "#528bcc", backgroundColor: "#11ffee00"}} stretched before={<Avatar size={28} src={this.state.game.photo1}/>} after={<Counter>{this.state.game.clicks1}</Counter>}>&#4448;{this.shortName(this.state.game.name1)}</Button>
							<Button level="outline" style={{height: "50px", borderBottomLeftRadius: "0px", borderBottomRightRadius: "20px", borderTopLeftRadius: "0px", borderTopRightRadius: "0px", borderWidth: "1", borderColor: "#528bcc", backgroundColor: "#11ffee00"}} stretched after={<Avatar size={28} src={this.state.game.photo2}/>} before={<Counter>{this.state.game.clicks2}</Counter>}>&#4448;{this.shortName(this.state.game.name2)}</Button>
						</div>

						<div style={{display: "flex", alignItems: "center", justifyContent: "center", marginTop: "65px"}}>
							{this.state.game_person}
						</div>
						<div style={{display: "flex", alignItems: "center", justifyContent: "center"}}>
							<img onClick={()=> socket.json.send({'method': 'getIce'})} src={ice} style={{height:"45%",width:"45%"}}/>
						</div>
					</Panel>
					<Panel id="error">
						<PanelHeader>{this.state.error.action}</PanelHeader>

						<Placeholder
							icon={<Icon56ErrorOutline />}
							action={<Div><Button onClick={this.componentDidMount} style={{margin: '8px'}} size="l" level="outline">Переподключиться</Button>
								<Button onClick={this.componentDidMount} style={{margin: '8px'}} stretched size="l" level="tertiary" component="a" href="https://vk.com/club173263813">Группа ВКонтакте</Button>
								</Div>}
							stretched
						>
							{this.state.error.text}
						</Placeholder>
					</Panel>

				</View>
			</View>
		);
	}
}

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Home;